import common from "../../common/common.js"
export default {
	data() {
		return {
			keywords: '',
			page: 1,
			pageSize: 15,
			total: 1,
			searchList: []
		}
	},
	onReachBottom() {
		// 判断是否是最后一页
		if (this.page == this.total) {
			return
		}
		
		this.page++
		this.getData()
	},
	methods: {
		getData() {
			uni.showLoading({
				mask: true,
				title: "请稍后..."
			});
			common.urlRequest('/search/list', {
				keywords: this.keywords,
				page: this.page,
				pageSize: this.pageSize
			}, 'POST', (res) => {
				console.log(res.data)
				this.searchList.push(...res.data.rows)
				this.total = res.data.total
			})
		},
		searchKeywords(e) {
			this.keywords = e.detail.value
			this.searchList = []
			this.getData()
		},
		toDetail(id) {
			// console.log(id)
			uni.navigateTo({
				url: `../detail/detail?moviceId=${id}`,
			});
		}
	},
	onLoad() {
		this.getData()
	}
}
